import { Component, OnInit } from '@angular/core';
import { ProductService } from '../../../../../auth/_services/product.service';
import { CategoryService } from '../../../../../auth/_services/category.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
import { SideBarService } from '../../../../layouts/side-bar/side-bar.service';
import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { DataService } from '../../../../../_services/data.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { ToastrService } from 'ngx-toastr';

@Component({
    selector: 'app-product-list',
    templateUrl: "product-list.component.html",
    styles: []
})
export class ProductsListComponent implements OnInit {


    dropDownValueSearch = [];

    dropDownValueStatus = '';
    dropDownValueType = '';
    public filterDate: any;
    public bulkActionData: any;
    public filterDataHolder: any;

    public categoryData = [{ "id": 1, "category_name": "HTC-test-a" }, { "id": 2, "category_name": "test" }];

    constructor(private toastr: ToastrService, private imgBaseUrl: ScriptLoaderService, private route: ActivatedRoute, private router: Router, private _dataService: DataService, private _productService: ProductService, private _categoryService: CategoryService) { }

    public productData = [{ "product_id": 1032, "slug": "HTC-test-a", "sku": "a", "product_name": "test", "availability": "in stock", "actual_price": 1000, "mrp": 1200, "current_price": 800, "stock_quantity": 4, "total_stock_quantity": 5, "description": "<p><strong>good<\/strong><\/p>", "specification": null, "product_details": null, "product_img": "7f48ccb34b1d8fcec8774ae9eeb6de33.png", "user_id": 1, "brand_id": 49, "discount_type": null, "discount": null, "discount_display_type": null, "shareable_product": "shareable", "status": "Active", "deleted_status": "Not deleted", "admin_commission_type": "flat", "admin_commission": 65, "vendor_commission_type": "flat", "vendor_commission": 65, "product_state": null, "country": null, "created_date": "2018-11-27", "updated_date": "2018-11-27 05:17:36", "max_order_qty": 0, "name": "HTC", "state": false }];

    public searchValue = "";
    public ImgUrl: any;
    public permission;
    ngOnInit() {
        let loggedUserId: any;
        let where: any;
        this.ImgUrl = this.imgBaseUrl._imgBaseUrl;


        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        this.permission = currentUser.success.permission;
        if (this.permission.product_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/


        this._categoryService.getAll().subscribe(
            data => {
                this.categoryData = data.success;
            }
        )

        this.route.params.subscribe(
            parameter => {
                localStorage.setItem('section', 'product');
                loggedUserId = JSON.parse(localStorage.getItem('currentUser'));
                if (parameter.vendor_id) {
                    console.log('parameter.vendor_id', parameter.vendor_id);
                    where = { loggedUserId: parameter.vendor_id, role: '' };
                } else {
                    where = { loggedUserId: loggedUserId.success.id, role: loggedUserId.success.role };
                }
                this._productService.getAll(where).subscribe(
                    data => {
                        this.productData = data.success;
                    },
                    (err: string) => {
                        console.log('sorry we got error in login');
                        localStorage.removeItem('currentUser');
                        localStorage.removeItem('subCategory');
                        localStorage.removeItem('productType');
                        localStorage.removeItem('menu');
                        this.router.navigate(['/login']);
                    }
                );
            }
        );
    }




    checkAll(ev, productData: any) {
        console.log(productData);
        console.log(ev);
        productData.forEach(x => x.state = ev.target.checked)
    }

    isAllChecked(productData: any) {
        ///console.log(productData);
        return productData.every(_ => _.state);
    }



    bulkAction(productData: any) {
        let cart_data = [];
        this.productData.forEach(obj => {
            console.log('obj');
            if (obj.state) {
                cart_data.push(obj.product_id);
            }
        });
        console.log('cart_data');
        console.log(cart_data);
        let id = 1;
        this._productService.productDeleteBulk(cart_data).subscribe(
            data => {
                if (Number(data) && data != 0) {
                    this.toastr.success('Changes updated successfully!', 'Success Message');
                    this.getAllProductList();
                } else {
                    this.toastr.error('Sorry! changes could not updated.', 'Error Message');
                }
            }
        );
    }

    public getAllProductList() {
        let loggedUserId: any;
        let where: any;
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        this.route.params.subscribe(
            parameter => {
                localStorage.setItem('section', 'product');
                loggedUserId = JSON.parse(localStorage.getItem('currentUser'));
                if (parameter.vendor_id) {
                    console.log('parameter.vendor_id', parameter.vendor_id);
                    where = { loggedUserId: parameter.vendor_id, role: '' };
                } else {
                    where = { loggedUserId: loggedUserId.success.id, role: loggedUserId.success.role };
                }
                this._productService.getAll(where).subscribe(
                    data => {
                        this.productData = data.success;
                    },
                    (err: string) => {
                        console.log('sorry we got error in login');
                        localStorage.removeItem('currentUser');
                        localStorage.removeItem('subCategory');
                        localStorage.removeItem('productType');
                        localStorage.removeItem('menu');
                        this.router.navigate(['/login']);
                    }
                );
            }
        );
    }

    public search() {
        console.log(this.searchValue);

        this._productService.searching({ search_value: this.searchValue, dropdown_value: this.dropDownValueStatus, shareable: this.dropDownValueType }).subscribe(
            data => {
                this.productData = data.success;
            }
        );

    }

    public filterByDate() {
        let product_data = [];

        if (this.filterDate == '') {
            this.productData = this.filterDataHolder;
        } else {
            if (this.filterDataHolder == undefined) {
                this.filterDataHolder = this.productData;
            } else {
                this.productData = this.filterDataHolder;
            }

            this.productData.forEach(obj => {
                //let created_date = obj.payment_date.split(' ');
                //created_date = created_date;
                if (obj.created_date == this.filterDate) {
                    product_data.push(obj);
                }
            });
            this.productData = product_data;
        }
    }


    public productDelete(del_id) {

        let value = confirm('Are you want to delete this product ?');
        if (value) {

            this._productService.productDelete(del_id).subscribe(
                data => {
                    this.productData = data.data;
                    this.toastr.success('Product Deleted Successfully', 'Success Message');
                }
            );
        }
    }

    public changeStatus(product: any) {

        if (product.status == 'Active') {
            product.status = 'Inactive';
            console.log('product.product_id');
            console.log(product.product_id);

            this._productService.updateProductStaus(product.product_id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        } else {
            product.status = 'Active';
            this._productService.updateProductStaus(product.product_id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        }
    }

    public changeType(product: any) {

        if (product.shareable_product == 'shareable') {
            product.shareable_product = 'non shareable';

            this._productService.updateProductStaus(product.product_id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        } else {
            product.shareable_product = 'shareable';
            this._productService.updateProductStaus(product.product_id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        }
    }
}
